.transition-5s{
    transition: all .5s;
}

.cursor-pointer{
    cursor: pointer;
}



// 照片辨正方型外框
.wrap-square{
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    overflow: hidden;
}
// 照片辨正方型照片
.square-img{
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50% ,-50%);
}

.pointer-event-none{
    pointer-events: none;/* 穿透上層 icon 以點擊下方按鈕 */
}

.more-deco{
    position: relative;
    &::after{
        position: absolute;
        content: "";
        height: 1px;
        width: 34px;
        background-color: $dark;
        top:52%;
        right: 0;
        transition: all .5s;
      
    }
    &:hover{
        &::after{
            background-color: $primary;
            width: 0;
        }
    }
}

.max-width-none{
    max-width: none;
}

.mask{
    position: relative;
    
    &::before{
        position: absolute;
        content: "more detail";
        color: $secondary;
        display: flex;
        justify-content: center;
        align-items: center;
        background:$dark;
        opacity: 0;
        transition: all .4s;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        font-size: 20px;
       font-family: 'Expletus Sans', cursive;
        letter-spacing: 4px;
    }
    &:hover{
        &::before{
        background:$mask-hover;
        opacity: 0.7;
        letter-spacing: 2px;
        }
    }
}
.btn-hover{
    position: relative;
    &::before{
        position: absolute;
        content: "";
        height: 100%;
        width: 0%;
        left: 0;
        top:0;
        background-color: $primary;
        transition: all .5s;
    }
    &::after{
        position: absolute;
        content: "關於我";
        top: 50%;
        left: 50%;
        transform: translate(-50% ,-50%);
        color: $secondary;
        opacity: 0;
        font-size: 20px;
        letter-spacing: 4px;
        transition: all .5s;
    }
    &:hover{
          &::before{
            background-color: $primary;
            width: 100%;
          }
          &::after{
            opacity: 1;
          }
        }
}


    